/**
 * stylestyle
 * 
 *  {
 *      fontSize: '30rpx',
 *      color   : '#ccc'
 *  }
 * 
 * 
 * "font-size:30rpx;color:#ccc;"
 */

export default style => 
    _.transform(style, function(result, value, key){
        result.push(_.kebabCase(key) + ':' + value + ';')
    }, [])
    .join('')